import React from 'react';
import {Field} from "redux-form";
import {Input} from "../common/FormsControl/FormsControls";
import {maxLenghtCreator, minLenghtCreator, required} from "../../Utils/Validators/validators";
import s from "../common/FormsControl/FormsControls.module.css"

const maxLength25 = maxLenghtCreator(25);
const minLength8 = minLenghtCreator(8)

const LoginForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field placeholder={"Email"} name={"email"} component={Input}
                validate={[required, maxLength25]}/>
            </div>
            <div>
                <Field placeholder={"Password"} name={"password"} component={Input} type={"password"}
                       validate={[required, maxLength25, minLength8]}/>
            </div>
            <div>
                <Field type={"checkbox"} name={"rememmberMe"} component={Input}/> rememmber me
            </div>
            {props.error && <div className={s.formSummaryError}>
                {props.error}
            </div>}
            <div>
                <button>Login</button>
            </div>
        </form>
    )
}


export default LoginForm;