import React from 'react';
import s from './ProfileInfo.module.css';
import Preloader from "../../../common/Preloader";
import userPhoto from "../../../../assets/images/user-icon.png";
import {NavLink} from "react-router-dom";
import jobPhoto from '../../../../assets/images/smail-1.png'
import jobPhoto2 from '../../../../assets/images/smail-2.png'
import ProfileStatus from "./ProfileStatus";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = (props) => {
    if (!props.profile) {
        return <Preloader/>
    }
    return (
        <div>

           {/* <div>
                <img
                    src='https://images.ctfassets.net/hrltx12pl8hq/6bi6wKIM5DDM5U1PtGVFcP/1c7fce6de33bb6575548a646ff9b03aa/nature-photography-pictures.jpg?fit=fill&w=800&h=300'></img>
            </div>*/}
            <div className={s.userInfo}>
                <div className={s.photoLarge}>
                    <img
                        src={props.profile.photos.large}></img>
                </div>
                <div>
                    <div>Обо мне: {props.profile.aboutMe}</div>
                    <div>Полное имя: {props.profile.fullName}</div>
                    <div>Мой Id: {props.profile.userId}</div>
                    <div>
                        <img src={props.profile.lookingForAJob ? jobPhoto : jobPhoto2}
                             className={s.userPhoto}/>
                    </div>
                    <div>Как я ищу работу: {props.profile.lookingForAJobDescription}</div>
                    <div>
                        <img src={props.profile.photos.small}/>
                    </div>
                    <div>
                        <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
                    </div>
                </div>
            </div>


        </div>)

}

export default ProfileInfo;