import React from 'react';
import Profiles from "./Profiles";
import {connect} from "react-redux";
import {getUserProfile, getStatus, updateStatus} from "../../redux/profile-reducer";
import {withRouter} from "react-router-dom";
import {compose} from "redux";


class ProfilesContainer extends React.Component {

    componentDidMount() {
        let userId = this.props.match.params.userId;
        if (!userId) {
            userId = this.props.autorisedUserId;
            if (!userId) {
                this.props.history.push("/login")
            }
        }
        this.props.getUserProfile(userId);
        this.props.getStatus(userId);

    }

    render() {
       return (
            <Profiles {...this.props} profile={this.props.profile}
                      status={this.props.status} updateStatus={this.props.updateStatus}/>
        )
    }
}

let mapStateToProps = (state) => {
    return {
        profile: state.profilePage.profile,
        status: state.profilePage.status,
        autorisedUserId: state.auth.userId,
        isAuth: state.auth.isAuth

    }
}

export default compose(connect(mapStateToProps, {getUserProfile, getStatus, updateStatus}),
    withRouter,/* withAuthRedirect*/)(ProfilesContainer);